
body {
  background-color: #eaeaea;
}
ul {
  list-style: none;
  font-size: 1.5em;
}
li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-bottom: 10px;
}
input[type=checkbox] {
  font-size: 1em;
  margin: 13px 0;
}
label {
  width: 100%;
  text-align: left;
  padding: 0 0 0 15px;
}
.todo-list {
  max-width: 400px;
  margin: 0 auto;
  background-color: #ffffff;
  padding: 15px;
  -webkit-box-shadow: 2px 2px 2px #888;
          box-shadow: 2px 2px 2px #888;
  border-radius: 3px;
}
input[type=text] {
  font-size: 1.4em;
  padding: 3px 6px;
}
.filters .active {
  background-color: #0074D9;
  border: none;
  outline: none;
  color: #ffffff;
}
.filters button {
  border: none;
  outline: none;
  font-size: 1em;
  padding: 5px;
}
.remove {
  background-color: transparent;
  border: none;
  font-size: 1em;
}
.remove:hover {
  cursor: pointer;
}



